// TodoList.js
import React, { useState, useEffect, useCallback } from 'react';
import {
  List,
  ListItem,
  ListItemText,
  Checkbox,
  IconButton,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import api from '../api/api';
import FilterOptions from './FilterOptions';

function TodoList() {
  const [todos, setTodos] = useState([]);
  const [activeFilter, setActiveFilter] = useState('all');

  const applyFilter = useCallback((todos) => {
    if (activeFilter === 'completed') {
      return todos.filter((todo) => todo.completed);
    }
    if (activeFilter === 'incomplete') {
      return todos.filter((todo) => !todo.completed);
    }
    return todos;
  }, [activeFilter]);

  const fetchTodos = useCallback(async () => {
    try {
      const fetchedTodos = await api.getTodos();
      const filteredTodos = applyFilter(fetchedTodos);
      setTodos(filteredTodos);
    } catch (error) {
      console.error('Error fetching todos:', error);
    }
  }, [applyFilter]);

  useEffect(() => {
    fetchTodos();
  }, [fetchTodos]);

  const deleteTodo = async (id) => {
    try {
      await api.deleteTodo(id);
      fetchTodos();
    } catch (error) {
      console.error('Error deleting todo:', error);
    }
  };

  const toggleCompleted = async (id, completed) => {
    try {
      await api.updateTodo(id, { completed: !completed });
      fetchTodos();
    } catch (error) {
      console.error('Error updating todo:', error);
    }
  };

  return (
    <div>
      <FilterOptions filter={activeFilter} setFilter={setActiveFilter} />
      <List>
        {todos.map((todo) => (
          <ListItem
            key={todo._id}
            alignItems="center"
            sx={{
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
            }}
          >
            <Checkbox
              edge="start"
              checked={todo.completed}
              tabIndex={-1}
              disableRipple
              onChange={() => toggleCompleted(todo._id, todo.completed)}
              sx={{ marginRight: 2 }}
            />
            <ListItemText
              primary={todo.description}
              sx={{
                textDecoration: todo.completed ? 'line-through' : 'none',
              }}
            />
            <IconButton
              edge="end"
              aria-label="delete"
              onClick={() => deleteTodo(todo._id)}
            >
              <DeleteIcon />
            </IconButton>
          </ListItem>
        ))}
      </List>
    </div>
  );
}

export default TodoList;
