import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  CssBaseline,
  Paper,
  Box,
} from '@mui/material';
import TodoList from './TodoList';
import AddTodoForm from './AddTodoForm';
import FilterOptions from './FilterOptions';
import api from '../api/api'; // Import your API functions from api.js

function Dashboard() {
  const [todos, setTodos] = useState([]);
  const [filter, setFilter] = useState('all'); // 'all', 'completed', 'incomplete'

  useEffect(() => {
    // Fetch todos from API based on the selected filter
    async function fetchTodos() {
      try {
        const response = await api.getTodos(filter);
        setTodos(response.data);
      } catch (error) {
        console.error('Error fetching todos:', error);
      }
    }

    fetchTodos();
  }, [filter]);

  // Function to add a new todo
  const addTodo = async (todo) => {
    try {
      const response = await api.addTodo(todo);
      setTodos([...todos, response.data]);
    } catch (error) {
      console.error('Error adding todo:', error);
    }
  };

  // Function to delete a todo
  const deleteTodo = async (id) => {
    try {
      await api.deleteTodo(id);
      setTodos(todos.filter((todo) => todo._id !== id));
    } catch (error) {
      console.error('Error deleting todo:', error);
    }
  };

  // Function to toggle the completed status of a todo
  const toggleCompleted = async (id, completed) => {
    try {
      await api.updateTodo(id, { completed: !completed });
      setTodos((prevTodos) =>
        prevTodos.map((todo) =>
          todo._id === id ? { ...todo, completed: !completed } : todo
        )
      );
    } catch (error) {
      console.error('Error updating todo:', error);
    }
  };

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <Paper elevation={3}>
        <Box p={3}>
          <Typography variant="h4" align="center" gutterBottom>
            ToDo App
          </Typography>
          <AddTodoForm addTodo={addTodo} />
          <FilterOptions filter={filter} setFilter={setFilter} />
          <TodoList
            todos={todos}
            deleteTodo={deleteTodo}
            toggleCompleted={toggleCompleted}
          />
        </Box>
      </Paper>
    </Container>
  );
}

export default Dashboard;
