import axios from 'axios';

const API_BASE_URL = 'http://localhost:5000/api'; // Update with your API URL

const api = axios.create({
  baseURL: API_BASE_URL,
});

// Fetch all todos based on the filter
export async function getTodos(filter) {
  try {
    const response = await api.get(`/todos?filter=${filter}`);
    return response.data;
  } catch (error) {
    throw error;
  }
}

// Add a new todo
export async function addTodo(todo) {
  try {
    const response = await api.post('/todos', todo);
    return response.data;
  } catch (error) {
    throw error;
  }
}

// Delete a todo by ID
export async function deleteTodo(id) {
  try {
    const response = await api.delete(`/todos/${id}`);
    return response.data;
  } catch (error) {
    throw error;
  }
}

// Update the completed status of a todo by ID
export async function updateTodo(id, updates) {
  try {
    const response = await api.patch(`/todos/${id}`, updates);
    return response.data;
  } catch (error) {
    throw error;
  }
}

export default {
  getTodos,
  addTodo,
  deleteTodo,
  updateTodo,
};
